.App {
	text-align: center;
}

.App-logo {
	animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
	pointer-events: none;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.album-item {
	border: 2px solid blue;
	border-radius: 10px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 20px;
	padding: 12px 8px;
}

.album-item h2 {
	margin-left: auto;
	margin-right: auto;
	max-width: 90%;
	text-align: center;
}

.album-item div {
	margin-left: auto;
	margin-right: auto;
	max-width: 95%;
	text-align: center;
	height: 190px;
	padding-top: 5px;
	padding-bottom: 5px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.album-item p {
	font-family: "Digital7";
	font-size: 20px;
	margin-bottom: 0;
	padding-bottom: 0;
	padding-top: 0;
}

.album-item > div > span[role="img"] {
	display: inline-block;
	height: 20px;
	font-size: 25px;
	margin-right: 5px;
	margin-inline: 5px;
	max-width: 35px;
	cursor: default;
}

details span[role="img"] {
	height: 22px;
	font-size: 22px;
	width: auto;
	cursor: default;
}
